
import { defineComponent, watchEffect, reactive, toRefs } from 'vue'
import axios from 'axios'

import PublishersCard from '@/components/GridCards/PublishersCard.vue'

export default defineComponent({
  setup() {
    const state = reactive({
      isLoading: false,
      results: [] as any,
      currentPage: 1,
      nextPage: String || null
    })

    watchEffect(() => {
      state.isLoading = true
      state.results = []
      const fetchItems = async () => {
        const response = await axios(
          `https://api.rawg.io/api/publishers?key=c0233312e0954f0ca72e3d1e9d64ef32&page=${state.currentPage}&page_size=20`
        )
        state.results.push(response.data.results)
        state.nextPage = response.data.next
        state.isLoading = false
      }
      fetchItems()
    })

    const getNextPage = () => {
      state.currentPage++
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
    const getPrevPage = () => {
      if (state.currentPage == 1) return
      state.currentPage--
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
    const getFirstPage = () => {
      state.currentPage = 1
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }

    return {
      ...toRefs(state),
      getNextPage,
      getPrevPage,
      getFirstPage
    }
  },
  components: {
    PublishersCard
  }
})
